/*-- Common Style, border box (padding + border + width/height maths */
*,
*::before,
*::after {
  box-sizing: border-box; }

html {
  font-size: 62.5%; }

html, body {
  height: 100%; }

html,
body {
  position: relative;
  width: 100%;
  height: 100%;
  background-color: #cacaca;
  color: black;
  -webkit-tap-highlight-color: black;
  -webkit-touch-callout: none;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

body {
  font-family: "Webly Sleek SemiLight", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 1.6rem; }

.main {
  height: 100%; }

.dashboard {
  margin-top: 40px; }

.section-centered {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 20px; }

.accesiblity-text {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0; }

.container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto; }

@media (min-width: 576px) {
  .container {
    max-width: 540px; } }

@media (min-width: 768px) {
  .container {
    max-width: 720px; } }

@media (min-width: 992px) {
  .container {
    max-width: 960px; } }

h1, h2, h3, h4, h5, h6 {
  font-weight: 500;
  line-height: 1.2;
  margin-top: 1rem;
  margin-bottom: 1rem; }

h1 {
  font-size: 2.8rem; }

h2 {
  font-size: 1.96rem;
  margin-bottom: 2.5rem; }

h3 {
  font-size: 1.82rem; }

h4 {
  font-size: 1.68rem; }

h5 {
  font-size: 1.54rem; }

h6 {
  font-size: 1.4rem; }

p {
  font-size: 17px;
  line-height: 1.3;
  margin-top: 0;
  margin-bottom: 1rem; }

.text-center {
  text-align: center !important; }

.card {
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 0;
  padding: 10px;
  margin-bottom: 20px;
  background-color: white;
  background-clip: border-box;
  word-wrap: break-word; }
  .card > header {
    display: flex; }
  .card .card-body {
    flex: 1 1 auto;
    padding: 2rem 1.75rem; }

/*--
    Button and submit button style
------------------------------------------*/
.btn {
  display: inline-block;
  font-family: non-seasonal sans, sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 1;
  padding: 0 40px;
  transition: all 0.3s ease 0s;
  text-align: center;
  min-height: 38px; }
  .btn:disabled {
    pointer-events: none; }

.btn-primary {
  color: white;
  background: #2c2c2c none repeat scroll 0 0;
  border: 2px solid #2c2c2c; }
  .btn-primary:hover, .btn-primary:focus {
    color: white;
    border-color: black;
    background: black;
    box-shadow: 0 5px 30px rgba(0, 0, 0, 0.15); }
  .btn-primary:focus {
    box-shadow: 0 5px 30px rgba(0, 0, 0, 0.15); }

.btn-default {
  background-color: #EDEEF2;
  border: 1px solid #dadada; }
  .btn-default:hover {
    background-color: white; }
  .btn-default:focus {
    box-shadow: 0 0 0 0.2rem rgba(150, 150, 150, 0.2); }

input {
  display: block;
  width: 100%;
  height: 38px;
  padding: 0.4rem 1rem;
  font-family: "Webly Sleek SemiLight", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 1.4rem;
  font-weight: 400;
  line-height: 1.4;
  background-color: white;
  border: 2px solid white;
  transition: border-color .15s ease-in-out, box-shadow .15s ease-in-out; }
  input:focus {
    outline: 0; }
  input::placeholder {
    color: #6c757d;
    opacity: 1; }

label {
  display: block;
  margin-bottom: .5rem; }

.form-group {
  margin-bottom: 1rem; }

.has-error .form-control {
  border-bottom: 2px solid #a94442; }

.has-error .help-block {
  color: white;
  display: block;
  margin-top: 5px;
  margin-bottom: 10px;
  font-size: 14px;
  text-align: left; }

.alert {
  position: relative;
  padding: 2rem 0 1rem;
  border-top: 1px solid white;
  text-align: left; }

.table {
  width: 100%;
  border: 1px solid #dadada;
  border-collapse: collapse;
  margin-bottom: 0; }
  .table th,
  .table td {
    padding: 1.2rem;
    vertical-align: middle;
    text-align: left;
    border: 1px solid #dadada; }
  .table thead th {
    vertical-align: middle;
    background-color: #a6a6a6;
    color: black;
    border-top: none;
    letter-spacing: .3px;
    text-align: center; }
  .table thead th,
  .table thead td {
    border-bottom-width: 2px; }
  .table tbody th {
    border-top: 1px solid #dadada;
    color: black; }
  .table tbody tr:nth-of-type(odd) {
    background-color: white; }

.table-responsive {
  display: block;
  width: 100%;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch; }

.session .card {
  background-color: #e27b21;
  width: 400px;
  padding: 40px 100px 20px; }

.session h2 {
  font-family: "Webly Sleek SemiBold", "Helvetica Neue", Helvetica, Arial, sans-serif; }

.session .actions-section {
  margin-top: 2rem;
  margin-bottom: 2rem;
  text-align: right; }

.search-section {
  position: relative;
  padding: 50px 20px 30px;
  background-color: #e27b21; }
  @media (min-width: 768px) {
    .search-section {
      padding-left: 70px;
      padding-right: 70px; } }
  .search-section form {
    display: flex;
    justify-content: space-between;
    align-items: flex-end; }
    .search-section form label {
      font-size: 2.2rem; }
    .search-section form .btn {
      margin-bottom: 10px; }

.search-results-section {
  background-color: #eeeeee;
  padding: 20px 20px; }
  .search-results-section h2 span, .search-results-section h3 {
    font-family: "Webly Sleek SemiBold", "Helvetica Neue", Helvetica, Arial, sans-serif;
    text-transform: uppercase; }

.pagination {
  display: flex;
  justify-content: flex-end;
  padding-top: 2rem;
  padding-left: 0;
  list-style: none; }

.page-link {
  position: relative;
  display: block;
  padding: 0.75rem 1.2rem;
  margin-left: -1px;
  font-size: 1.5rem;
  line-height: 1.25;
  color: black;
  background-color: #fff;
  border: 1px solid #dee2e6; }
  .page-link:hover {
    z-index: 2;
    color: black;
    text-decoration: none;
    background-color: #e9ecef;
    border-color: #dee2e6; }
  .page-link:focus {
    z-index: 2;
    outline: 0;
    box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25); }

.page-item:first-child:first-child .page-link {
  margin-left: 0;
  border-top-left-radius: 0.25rem;
  border-bottom-left-radius: 0.25rem; }

.page-item:first-child:last-child .page-link {
  border-top-right-radius: 0.25rem;
  border-bottom-right-radius: 0.25rem; }

.page-item:first-child.active .page-link {
  z-index: 1;
  color: #fff;
  background-color: black;
  border-color: black; }

.page-item:first-child.disabled .page-link {
  color: #6c757d;
  pointer-events: none;
  cursor: auto;
  background-color: #fff;
  border-color: #dee2e6; }
